import { IAction, IActionContext, IActionInput, ICreateActionContext } from '@msdyn365-commerce/core-internal';
import { createObservableDataAction } from '../../create-data-action.vnext';

async function getError(input: IActionInput, ctx: IActionContext): Promise<string> {
    ctx.trace('Get Error was called!!!');
    throw new Error('Get Error was called');
}

function createInputs(arg: ICreateActionContext<any>) {
    return arg.config;
}

/*@internal*/
export default createObservableDataAction({ action: getError as IAction<string>, input: createInputs });
